        <span class="hljs-tag">&lt;<span class="hljs-name">md-option</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in options"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"item.value"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"item.value"</span>&gt;</span>
          {{item.label}}
        <span class="hljs-tag">&lt;/<span class="hljs-name">md-option</span>&gt;</span>
